<template>
    <div class="body">
        <div class="cen" v-loading="loading" element-loading-text="正在登陆">
            <div>
                <img src="../../assets/wcs-1.svg" alt="">
            </div>
            <div>
                <table>
                    <tr>
                        <td class="title">账号</td>
                        <td>
                            <el-input v-model="username" placeholder="请输入账号"></el-input>
                        </td>
                    </tr>
                    <tr>
                        <td class="title">密码</td>
                        <td>
                            <el-input placeholder="请输入密码" v-model="userpassword" show-password></el-input>
                        </td>
                    </tr>
                </table>
            </div>
            <div>
                <button class="sbmit" @click="submit()">登陆</button>
            </div>
        </div>
    </div>
</template>

<script>
import { Notification } from 'element-ui'
import store from '@/store'
export default {
  name: 'userLogin',
  data () {
    return {
      username: '',
      userpassword: '',
      loading: false,
      loadingText: '正在登陆'
    }
  },
  methods: {
    submit () {
      if (this.username === '') {
        Notification({
          title: '警告',
          message: '请输入正确的账号',
          type: 'warning'
        })
      } else if (this.userpassword === '') {
        Notification({
          title: '警告',
          message: '请输入正确的密码',
          type: 'warning'
        })
      } else {
        this.loading = true
        setTimeout(() => {
          Notification({
            title: '成功[测试用户]',
            message: '欢迎回来：George（' + this.username + ')',
            type: 'success'
          })
          store.commit('userNameUpdate', this.username)
          setTimeout(() => {
            this.$router.push('/ZongtiGaishu')
          }, 500)
        }, 500)
      }
    }
  }
}
</script>

<style lang="less" scoped>
.body{
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content:center;
    align-items: center;
    //background-color: skyblue;
    background-image:url(../../assets/looginBgc.svg) ;
    background-repeat: no-repeat;
    background-position: bottom;
}
.cen{
    width: 700px;
    height: 400px;
    background-color: rgba(255, 255, 255, 0.7);
    display: flex;
    flex-direction: column;
    align-items: center;
    color: black;
    border-radius: 5px;
    backdrop-filter: blur(10px);
    img{
        width: 100px;
        height: 100px;
        margin-top: 50px;
    }
    table{
        margin-top: 15px;
        .title{
            font-size: 18px;
            font-weight: bold;
            padding-right: 10px;
        }
        tr{
            height: 60px;
        }
    }
    .sbmit{
        margin-top: 15px;
        width: 100px;
        height: 50px;
        border-radius: 15px;
        background-color: rgb(65, 117, 247);
        border: 0;
        color: white;
    }
    .sbmit:hover{
        background-color: rgb(41, 101, 253);
    }
}
</style>
